import { getApiUrlWithToken, Logger } from '@uniformdev/common';
import { UniformServerConfig } from '../UniformServerConfig';

export function attachMediaMiddleware(server: any, uniformConfig: UniformServerConfig, logger: Logger) {
    server.get(['/-/media/*', '/-/jssmedia/*'], getMediaMiddleware(uniformConfig, logger));
}

export function getMediaMiddleware(uniformConfig: UniformServerConfig, logger: Logger) {
    return async function (req: any, res: any) {
        const url = getApiUrlWithToken(uniformConfig, decodeURI(req.path));

        logger.debug('Redirecting request to ' + url);
        res.redirect(url);
    };
}
